.header_fixed {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 2;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  height: 45px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(8px);

  box-shadow: 1px 7px 8px 0px rgba(37, 37, 37, 0.38);
  -webkit-box-shadow: 1px 7px 8px 0px rgba(37, 37, 37, 0.38);
  -moz-box-shadow: 1px 7px 8px 0px rgba(37, 37, 37, 0.38);
}
